import Vue from 'vue' 
import router from './router'
import App from './App.vue'
import ApiConfig from '@/apiConfig.js'
import * as utils from '@/common/utils.js' 
import Axios from '@/common/request.js'  
import { Toast,Tab, Tabs, List, Lazyload, Swipe, SwipeItem,Field, Cell, CellGroup, Picker,Popup  } from 'vant'
import '@/assets/scss/normal.scss';

Vue.use(Toast).use(Tab).use(Tabs).use(List).use(Swipe).use(SwipeItem);
Vue.use(Field).use(Cell).use(CellGroup).use(Picker).use(Popup);


const defaultImg = require('@/assets/images/party/default.png');
Vue.use(Lazyload,{loading: defaultImg, error: defaultImg});

import { PullRefresh } from 'vant'; 
Vue.use(PullRefresh); 

Vue.prototype.axios = Axios

router.beforeEach((to, from, next) => {
  if (!!to.meta.pageTitle && typeof to.meta.pageTitle !== undefined) {
    utils.setPageTitle(to.meta.pageTitle)
  }
  next()
})

Vue.config.productionTip = false 

new Vue({
    el: '#app',
    router,
    render: h => h(App)
})