import Vue from 'vue'
import Router from 'vue-router' 

const Home = () => import('../views/Home.vue')
const Build = () => import('../views/Build.vue')
const Application= () => import('../views/Application.vue')

Vue.use(Router)

export default new Router({
  routes: [ {
    path: '/',
    component: Home,
    name: 'pHome',
    meta: {
        pageTitle: '智慧党建'
    }
   },{
    path: '/build',
    component: Build,
    name: 'pBuild',
    meta: {
        pageTitle: '党支建设'
    }
   },{
    path: '/application',
    component: Application,
    name: 'pApplication',
    meta: {
        pageTitle: '入党申请'
    }
   },{
    path: '/transfer',
    component: () => import('../views/Transfer.vue'),
    name: 'transfer',
    meta: {
        pageTitle: '组织关系转移'
    }
   },{
    path: '/idenauth',
    component: () => import('../views/Idenauth.vue'),
    name: 'idenauth',
    meta: {
        pageTitle: '党员身份认证'
    }
    },{
      path: '/news/:newsId',
      component: () => import('../views/News.vue'),
      name: 'news',
      meta: {
          pageTitle: '新闻详情'
      }
    },{
      path: '/process',
      component: () => import('../views/Process.vue'),
      name: 'process',
      meta: {
          pageTitle: '入党流程'
      }
    },
]
})
